export default function CytglistCtrl($stateParams) {
  var vm = this;
  vm.id = $stateParams.id;
  vm.list = [
    {name : '', key : 'title', width : '30%'},
    {name : '', key : 'quote', width : '20%'},
    {name : '', key : 'address', width : '20%'},
    {name : '', key : 'worktime', width : '20%'},
  ];
}


